<template>
  <x-crud :loading="loading">
    <x-form v-model="formData" :values="formData" @submit="onsubmit">
      <a-row :gutter="[24, 24]">
        <a-col
          :md="{ span: 22, offset: 0 }"
          :lg="{ span: 22, offset: 0 }"
          :xl="{ span: 22, offset: 0 }"
          class="col-form"
        >
          <x-input
            label="Название"
            name="name"
            type="text"
            validation="required"
            :disabled="disabled"
          ></x-input>
          <x-input
          label="Подсказка для предметов"
          name="first_help_text"
          type="textarea"
          :disabled="disabled"
          :validation="`required|max:${textareaMaxLength},length`"
          :help="
            formData.first_help_text &&
            `Максимально ${textareaMaxLength}. ${
              textareaMaxLength - formData.first_help_text.length <= 0
                ? 0
                : textareaMaxLength - formData.first_help_text.length
            } символось осталось.`
          "
        ></x-input>
        <x-input
          label="Подсказка для файлов"
          name="second_help_text"
          type="textarea"
          :disabled="disabled"
          :validation="`required|max:${textareaMaxLength},length`"
          :help="
            formData.second_help_text &&
            `Максимально ${textareaMaxLength}. ${
              textareaMaxLength - formData.second_help_text.length <= 0
                ? 0
                : textareaMaxLength - formData.second_help_text.length
            } символось осталось.`
          "
        ></x-input>
          
        </a-col>
        <a-col span="6">
          <div class="crud-form-button">
            <a-button type="primary" block html-type="submit">
              {{ getButtonAction }}
            </a-button>
          </div>
        </a-col>
      </a-row>
    </x-form>
  </x-crud>
</template>

<script>
import crudMixin from '@/mixins/crudMixin'
export default {
  props: ['action', 'id'],
  mixins: [crudMixin],
  data() {
    return {
      formData: {},
      entityName: 'indicator-report-card',
      textareaMaxLength: 1024,
    }
  },
  methods: {
    async onsubmit() {
      if (this.action === 'read') {
        this.$router.push(`/indicator-report-card/edit/${this.id}`)
        return
      }
      await this.saveEntity(this.formData)
    },
  },
  async created() {
    if (this.action !== 'create') await this.getEntity()
    this.loading = false
    console.log(this.action, this.id, this.disabled)
  },
}
</script>

